@import url(https://use.typekit.net/mld7xsq.css);
body{font-family:ivystyle-sans,sans-serif;margin:0}h1,h2,h3,h4,h5,h6{font-family:ivystyle-sans,sans-serif;font-weight:300;font-style:normal;line-height:140%;margin-bottom:1rem}p{font-size:15px;font-weight:400}.map-container{height:100vh}.nsc-card{position:fixed;top:50%;left:50%;transform:translate(-50%, -50%);width:735px;height:auto;max-width:90vw;padding:60px 30px 30px;background:rgba(255,255,255,.75);box-shadow:0px 10px 20px rgba(0,0,0,.15),0px 64px 64px rgba(219,219,219,.16);-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px);border-radius:12px;display:flex;flex-direction:column;justify-content:center;align-items:center}.nsc-card svg{margin-bottom:30px}@media(min-width: 992px){.nsc-card{padding:100px 100px 50px}.nsc-card svg{margin-bottom:30px}}
